<template>
  <div>
    <div class="videoBox">
      <video
        id="video"
        class="pic_video"
        playsinline
        autoplay
        x5-video-player-type="h5"
      ></video>
      <div class="scan" v-if="isShow">
        <div class="line" ref="line"></div>
      </div>
      <div class="back"></div>
      <div class="cover" v-if="isShow" ref="cover"></div>
      <div
        v-cloak
        style="position:fixed; top:0"
      >
        <div class="header">人脸识别</div>
        <div
          class="des"
        >
          <p style="font-size:14px;font-weight:600;">确认某*某本人操作</p>
          <p style="font-size:12px;margin-top:5px">保持正脸在取景框中根据屏幕指示完成</p>
        </div>
      </div>
    </div>
    <div style="width:10px;height:10px;overflow:hidden">
    <canvas
      id="canvas"
      class="canvas_pic"
    ></canvas>
    </div>

  </div>
</template>

<script>
require("tracking/build/tracking-min.js");
require("tracking/build/data/face-min.js");
require("tracking/build/data/mouth-min.js");
//   require('tracking/examples/assets/stats.min.js')
import axios from 'axios';

export default {
  name: "login",

  data() {
    return {
     isShow:false,
    };
  },

  mounted() {
    this.moveToCameraAVG();
  },

  methods: {
    // 头像相机
    moveToCameraAVG() {
      var self = this;
      if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {};
      }
      if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function(constraints) {
          var getUserMedia =
            navigator.getUserMedia ||
            navigator.webkitGetUserMedia ||
            navigator.mozGetUserMedia ||
            navigator.msGetUserMedia ||
            navigator.oGetUserMedia;
          if (!getUserMedia) {
            return Promise.reject(
              new Error("没有开启摄像头权限或浏览器版本不兼容")
            );
          }
          return new Promise(function(resolve, reject) {
            getUserMedia.call(navigator, constraints, resolve, reject);
          });
        };
      }
      if (window.stream) {
        window.stream.getTracks().forEach(track => {
          track.stop();
        });
      }
      var constraints = (window.constraints = {
        audio: false,
        video: {
          sourceId: "default",
          facingMode: { exact: "user" }
        }
      });
      navigator.mediaDevices
        .getUserMedia(constraints)
        .then(function(stream) {
          var video = document.getElementById("video");
          try {
            window.stream = stream;
            video.srcObject = stream;
          } catch (error) {
            video.src = window.URL.createObjectURL(stream);
          }
          self.localMediaStream = stream;
          video.play();
          self.isShow = true;

          //定时发送截图
         self.captureAvg();
        })
        .catch(function(err) {
          alert(err.name + ": " + err.message);
        });
    },
    //停止摄像机
    stopCapture: function() {
      var video = document.getElementById("video");
      if (!video.srcObject) return;
      let stream = video.srcObject;
      let tracks = stream.getTracks();
      tracks.forEach(track => {
        track.stop();
      });
    },
    // 头像照片
    captureAvg() {
      var vm = this;
      var video = document.getElementById("video");
      var canvas = document.getElementById("canvas"),
        ctx = canvas.getContext("2d"),
        CHeight = video.clientHeight, //获取屏幕大小让canvas自适应
        CWidth = video.clientWidth;
      canvas.width = CWidth;
      canvas.height = CHeight;
       let time = null;
          time = setTimeout(() => {
            if (vm.localMediaStream) {
              ctx.drawImage(video, 0, 0, CWidth, CHeight);
              var dataURL = canvas.toDataURL("image/jpeg"); //dataURL = 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAA'
              ctx.clearRect(0, 0, canvas.width, canvas.height);
              // console.log(dataURL)
              const Id_base64 = ``
              axios.post('/api/userAuth/faceauth',{
                Id_base64:Id_base64,
                Live_base64:dataURL.split(',')[1]
              },{
                headers:{
                  "token":"eyJhbGciOiJIUzUxMiIsInppcCI6IkdaSVAifQ.H4sIAAAAAAAAAKtWKi5NUrJSMjQ1MjI2MTE0MTdX0lFKrShQsjI0MzA3NzUwMTetBQCbmuMVJgAAAA.hjf4qWksdRMqq4-Omc3SpADs1Nte-D7XwyiRJ0NGTfJnfbWxDjfTBCn9CWFg7nii8Y-lWvc6MWkB6U5tWCuT0g",
                }
              }).then(res=>{
                const code=res.data.code;
                if(code == 200) { //识别成功
                  // 停止摄像机、动画
                  video.pause();
                  vm.stopCapture();
                  vm.$refs.line.style['animation-play-state']='paused';
                  vm.$refs.cover.style['animation-play-state']='paused';

                  // 保存认证信息


                }else if(code == 403){ //不相似


                }else {
                  
                }
              })
            }
          }, 3000);
    }
  }
};
</script>

